import SearchIcon from '@mui/icons-material/Search'
import {
  Box,
  Container,
  FormControl,
  Grid,
  IconButton,
  InputAdornment,
  InputLabel,
  MenuItem,
  OutlinedInput,
  Pagination,
  Select,
  Stack,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'
import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useSearchParams } from 'react-router-dom'
import ClubList from 'src/components/ClubList'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import categories from 'src/data/categories'
import types from 'src/data/types'

interface SearchAPIResponse {
  clubs: {
    id: number
    name: string
    type: string
    category: string
  }[]
  numPages: number
}

function Search() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const [searchParams, setSearchParams] = useSearchParams()
  const [params, setParams] = useState({
    name: searchParams.get('name') ?? '',
    category: searchParams.get('category') ?? '',
    type: searchParams.get('type') ?? '',
    page: searchParams.has('page') ? parseInt(searchParams.get('page')) : null
  })
  const [clubs, setClubs] = useState(null)
  const [numPages, setNumPages] = useState(null)
  const { enqueueSnackbar } = useSnackbar()

  const handleSubmit = event => {
    event.preventDefault()
    const search = document.getElementById('search') as HTMLInputElement
    setParams({ ...params, name: search.value, page: null })
  }
  const handleChange = event => setParams({ ...params, [event.target.name]: event.target.value, page: null })
  const handlePageChange = (event, value) => setParams({ ...params, page: value == 1 ? null : value })

  useEffect(() => {
    const newParams = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.type ? { type: params.type } : null,
      params.page ? { page: params.page } : null
    )
    setSearchParams(newParams)
  }, [params])

  useEffect(() => {
    const apiParams = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.type ? { type: params.type } : null,
      params.page ? { page: params.page } : null,
      matches ? null : { count: 10 }
    )
    axios
      .get<SearchAPIResponse>(`${process.env.REACT_APP_APIHOST}/search/clubs`, {
        params: apiParams
      })
      .then(res => {
        setNumPages(res.data?.numPages)
        setClubs(res.data?.clubs ?? [])
        if (res.data?.clubs?.length === 0)
          enqueueSnackbar('주어진 조건에 해당하는 동아리가 없어요 T.T', { variant: 'warning' })
      })
      .catch(err =>
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }, [searchParams])

  return (
    <>
      <Helmet>
        <title>검색 결과</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container alignItems="center">
          <Grid item>
            <Stack direction="row" alignItems="baseline">
              <Typography variant="h3" component="h3" gutterBottom sx={{ marginRight: 1 }}>
                {params.type === '' ? '검색 결과' : types[params.type]}
              </Typography>
              <Typography variant="inherit" gutterBottom>
                {params.category}
              </Typography>
            </Stack>
            <Typography variant="subtitle2">키워드와 카테고리, 분류를 활용해 원하는 동아리를 찾아보세요</Typography>
          </Grid>
        </Grid>
        <Box component="form" onSubmit={handleSubmit}>
          <FormControl variant="outlined" fullWidth sx={{ marginTop: 2, backgroundColor: 'white' }}>
            <OutlinedInput
              id="search"
              type="search"
              placeholder="동아리 이름을 검색해보세요"
              defaultValue={params.name}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton aria-label="Search clubs" type="submit" edge="end">
                    <SearchIcon />
                  </IconButton>
                </InputAdornment>
              }
            />
          </FormControl>
        </Box>
      </PageTitleWrapper>
      <Container
        sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', alignItems: 'center' }}
      >
        <Stack
          width="100%"
          direction={matches ? 'row' : 'column'}
          spacing={1}
          justifyContent={'flex-end'}
          sx={{ marginBottom: 3 }}
        >
          <FormControl size="small" fullWidth={!matches}>
            <InputLabel id="category-label">카테고리</InputLabel>
            <Select
              labelId="category-label"
              id="category"
              name="category"
              label="카테고리"
              value={params.category}
              onChange={handleChange}
              sx={{ minWidth: '180px' }}
            >
              <MenuItem value="">선택하지 않음</MenuItem>
              {categories?.map(c => (
                <MenuItem key={c} value={c}>
                  {c}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <FormControl size="small" fullWidth={!matches}>
            <InputLabel id="type-label">분류</InputLabel>
            <Select
              labelId="type-label"
              id="type"
              name="type"
              label="분류"
              value={params.type}
              onChange={handleChange}
              sx={{ minWidth: '180px' }}
            >
              <MenuItem value="">선택하지 않음</MenuItem>
              {types &&
                Object.keys(types).map(k => (
                  <MenuItem key={k} value={k}>
                    {types[k]}
                  </MenuItem>
                ))}
            </Select>
          </FormControl>
        </Stack>
        <ClubList clubs={clubs} />
        <Pagination
          page={params.page ?? 1}
          count={numPages ?? 1}
          size={matches ? 'medium' : 'small'}
          onChange={handlePageChange}
          sx={{ my: 3 }}
        />
      </Container>
    </>
  )
}

export default Search
